<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟到达
        </div>
        <div class="support" v-if="seller.supports">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div class="support-count" @click="showDetail" v-if="seller.supports">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-zuoyoujiantou iconfont"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetail">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-zuoyoujiantou iconfont"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" alt="">
    </div>
    <transition name="fade">
      <div class="detail" v-show="detailShow" transition="fade">
        <h1>{{seller.name}}</h1>

        <star :size="48" :score="seller.score"></star>

        <p>优惠信息</p>
        <div  v-for="(item,index) in seller.supports" class="desdes">
          <span class="icon" :class="classMap[seller.supports[index].type]"></span>
          <span class="text">{{item.description}}</span>
        </div>
        <div class="bulletin">{{seller.bulletin}}</div>
        <p @click="hideDetail" class="hideDetails">x</p>
      </div>
    </transition>
  </div>
</template>

<script>
  import star from '../star/star'
  export default {
    name: "top",
    props: {
      seller: {
        type: Object,
      }
    },
    components:{
      star
    },
    data() {
      return {
        detailShow: false
      }
    },
    created(){
      this.classMap=['decrease','discount','special','invoice','guarantee'];
    },
    methods: {
      add() {
        console.log(this.seller)
      },
      showDetail(){
        this.detailShow=true;
      },
      hideDetail(){
        this.detailShow=false;
      }
    }
  }

</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import '../../common/stylus/mixin.styl';
  .header
    position relative
    color: #fff
    overflow hidden
    background: rgba(7, 17, 27, 0.5)
    .content-wrapper
      position relative
      padding: 24px 12px 18px 24px
      font-size: 0
      .avatar
        display: inline-block
        vertical-align top
        img
          border-radius 2px
      .content
        display: inline-block
        font-size: 14px
        margin-left 16px
        .title
          margin 2px 0 8px 0
          .brand
            display inline-block
            vertical-align top
            width 30px
            height 18px
            bg-image('brand')
            background-size 30px 18px
            background-repeat no-repeat
          .name
            margin-left 6px
            font-size 16px
            line-height 18px
            font-weight bold

        .description
          margin-bottom 10px
          line-height 12px
          font-size 12px
        .support
          .icon
            display inline-block
            width 12px
            height 12px
            vertical-align middle
            margin-right 4px
            background-size 12px 12px
            background-repeat no-repeat
            &.decrease
              bg-image('decrease_1')
            &.discount
              bg-image('discount_1')
            &.guarantee
              bg-image('guarantee_1')
            &.invoice
              bg-image('invoice_1')
            &.special
              bg-image('special_1')
          .text
            line-height 12px
            font-size 12px
      .support-count
        position absolute
        right 12px
        bottom 14px
        padding 0 8px
        height 24px
        line-height 24px
        border-radius 12px
        background rgba(0, 0, 0, 0.2)
        text-align content
        .count
          font-size 10px
          vertical-align top
        .icon
          margin-left 2px
          line-height 24px
          font-size 10px
    .bulletin-wrapper
      position relative
      background-color rgba(7, 17, 27, 0.2)
      height 28px
      line-height 28px
      padding 0 22px 0 12px
      white-space normal
      overflow hidden
      text-overflow ellipsis
      .bulletin-title
        display inline-block
        vertical-align top
        margin-top 8px
        width 22px
        height 12px
        bg-image('bulletin')
        background-size 22px 12px
        background-repeat no-repeat
      .bulletin-text
        vertical-align top
        margin 0 4px
        font-size 10px
      .icon
        position absolute
        font-size 20px
        right 12px
        top: 0px
    .background
      position absolute
      top: 0
      left 0
      width 100%
      height 100%
      z-index -1
      filter blur(10px)
    .detail
      position fixed
      text-align center
      z-index 10000
      top 0
      right 0
      width 100%
      height 100%
      overflow auto
      background rgba(7, 17, 27, 0.8)
      -webkit-backdrop-filter blur(10px)
      &.fade-enter-active
        animation: bounce-in 0.5s;
      &.fade-leave-active
        animation: bounce-out .5s;
      @keyframes bounce-in{
        0% {
          transform scale(0)
        }
        50% {
          transform scale(1.5)
        }
        100% {
          transform scale(1)
        }
      }
      @keyframes bounce-out{
        0% {
          transform scale(1)
        }
        50% {
          transform scale(1.5)
        }
        100% {
          transform scale(0)
        }
      }
      .icon
        width 12px
        height 12px
        display inline-block
        background-repeat no-repeat
        background-size 12px 12px
        background-position center
        &.decrease
          bg-image('decrease_1')
        &.discount
          bg-image('discount_1')
        &.guarantee
          bg-image('guarantee_1')
        &.invoice
          bg-image('invoice_1')
        &.special
          bg-image('special_1')
      h1
        padding-top 70px
      .star
        padding 30px 0
      p
        font-size 20px
        padding-bottom 20px
      .desdes
        width: 75%
        margin: 0 auto
        text-align: left
        font-size 16px
      .bulletin
        width 80%
        font-size 16px
        margin 20px auto
        line-height 24px
      .hideDetails
        font-size 30px
</style>




